import { Box, Button, Heading, Text } from '@chakra-ui/react'

import React from 'react'

function ProjectCard({item}) {
  return (
    <div>
        <Box>
              <Box id="screen">
                <img src={item.image} alt="Me" />
              </Box>
              <Box id="sep" > HP </Box>
              <br />
              <Box>
                <Heading style={{"marginBottom":"10px"}}>{item.title}</Heading>
                <Text textAlign={'justify'} fontSize="2xl" m='auto' style={{"marginBottom":"10px", 'width':'80%'}}>
                 {item.description}                  
                </Text>
                <Box id="tech_stack">
                <Heading as={"h3"} size={"md"} >Tech Stacks: </Heading>
                  {item.techstack}
                  </Box>
               <Box className="btn">
                  <a href={item.githubLink}><Button><i class="fa-brands fa-square-github"></i></Button></a>
                  <a href={item.previewLink}><Button><i class="fa-sharp fa-solid fa-desktop"></i></Button></a>
               </Box>
              </Box>
            </Box>
    </div>
  )
}

export default ProjectCard