import {
  Box,
  Button,
  ButtonGroup,
  Flex,
  Spacer,
  Text,
} from '@chakra-ui/react';
import resume from "../assets/Avdhoot-Kurhe-Resume.pdf"
import React from 'react';
import { slide as Menu } from 'react-burger-menu';
import { logos } from '../Details';

export const Navbar = () => {
  return (
    <div>
      <Box id="menu">
        <Menu>
          <a href="#about">About</a>
          <a href="#skills">Skills</a>
          <a href="#projects">Projects</a>
          <a href="#contact">Contact</a>
        </Menu>
      </Box>

      {/* =========== */}
      <Flex id="navbar">
        <Box p="2">
          <a href="#home">
            <img id='R' src={logos.logo} alt="" />
          </a>
        </Box>
        <Spacer />
        <ButtonGroup gap="7">
          <a href="#about">
            <Button background={'none'} id={'hBtn'} color={'black'}>
              <Text>About</Text>
            </Button>
          </a>
          <a href="#skills">
            <Button background={'none'} id={'hBtn'} color={'black'}>
              <Text>Skills</Text>
            </Button>
          </a>
          <a href="#projects">
            <Button background={'none'} id={'hBtn'} color={'black'}>
              <Text>Projects</Text>
            </Button>
          </a>
          <a href="#contact">
            <Button background={'none'} id={'hBtn'} color={'black'}>
              <Text>Contact</Text>
            </Button>
          </a>
          <a href={resume} download>
            <Button background={'none'} id={'hBtn'} color={'black'} onClick={()=>{window.open(resume)}}><Text>Resume</Text></Button>
            </a>
        </ButtonGroup>
      </Flex>
    </div>
  );
};
