import { Box, Heading } from '@chakra-ui/react';
import React from 'react';
import img from '../assets/profile.png';
import { personalDetails } from '../Details';

function About() {
  return (
    <div>      
      <Box id="about">
      <Heading as={"h1"} align={'center'} color={'orangered'} m="2rem" size={"2xl"}> : About Me : </Heading>
        <Box id="about-para">
          <Box>
            <p>
              {personalDetails.about}
            </p>
          </Box>
          <Box>
            <img id='profile' src={img} alt="" />
          </Box>
        </Box>
      </Box>
    </div>
  );
}

export default About;
