import { Box, Heading} from '@chakra-ui/react';
import React from 'react';
import { contactDetails, socialMediaUrl } from '../Details';

export const Contact = () => {
  return (
    <div id='contact'>
      <Box>
        <Box>
          <Heading size={'2xl'} textAlign="center" color={'orangered'}> : Contact Me :</Heading>
        </Box>
        <br />
        <br />
        <Box id="contact_grid">
          <Box>
            <a href={socialMediaUrl.github}><i class="fa-brands fa-github"></i></a>
          </Box>
          <Box>
            <a href={socialMediaUrl.linkdein}><i class="fa-brands fa-linkedin"></i></a> 
          </Box>
          <Box>
            <a href={`mailto: ${contactDetails.email}`}><i class="fa-regular fa-envelope"></i></a>
          </Box>
          <Box style={{"display":"flex", "alignItems":"center", "gap":"10px"}}>
         <a href={`tel:${contactDetails.phone}`}> <i class="fa-sharp fa-solid fa-phone"></i></a>
          </Box>
        </Box>
      </Box>
    </div>
  );
};
